<script>
export default {
  name:'theme',
  data () {
    return {
      DisplayPanel: false,
      light_colors:[
        '#047aed',
        '#00365a','#008ecc',
        '#576a3d','#6fb327',
        '#922c88','#ed7117',
        '#900604','#c0a145',
        '#48494b'
      ],
      dark_colors:[
        '#047aed',
        '#00365a','#008ecc',
        '#576a3d','#6fb327',
        '#922c88','#ed7117',
        '#900604','#c0a145',
        '#48494b'
      ]
    }
  },
  methods: {
    changeColor (color) {
      document.documentElement.style.setProperty('--primary-color', color);
      document.getElementById('Capa_1').style.fill = color
    }
  }
}
</script>

<style>
.theme {
  position: fixed;
  top:30%;
  right: 0px;
  width: 250px;
  height: 220px;
  background-color: white;
  z-index: 101;
  border-radius: 5px;
  transition:transform 0.3s ease;
  -webkit-transition:transform 0.3s ease;
}

.theme div {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.theme div div {
  margin: 10px 15px;
}

.theme ul {
  flex-wrap: wrap;
  margin-left: -15px;
}

.theme ul li{
  margin:0 15px 10px 0;
  width: 25px;
  height: 25px;
  
}

.cycle-color {
  width: 100%;
  height: 100%;
  border-radius: 20px;
}

.theme ul li:hover .cycle-color {
  border: 3px solid;
  background-color: #fff !important;
}


.magic-wand {
  position: absolute;
  left: -50px;
  top: 40%;
  background-color: white;
  padding: 5px;
  width: 50px;
  height: 50px;
  border-bottom-left-radius: 5px;
  border-top-left-radius: 5px;
  cursor: pointer;
}

.magic-wand svg{
  width: 40px;
  height: 40px;
}

#Capa_1 {
  fill:var(--primary-color);
}


.panel-header{
  font-weight: 100;
  color: #444;
}


.slide-right{
  transform: translateX(250px);
}

</style>
