<template>
  <header id="header">
    <div class="navbar">
      <div class="container flex">
        <h1 class="logo">نمونه</h1>
        <nav>
          <ul>
            <li>
              <router-link to="/">خانه</router-link></li>
            <li> <router-link to="/fetures">ویژگی ها</router-link></li>
             <li> <router-link to="/Documents">مستندات</router-link></li>
          </ul>
        </nav>
      </div>
    </div>
  </header>
</template>

<script>

export default {
  name:'app-header',
  data () {
    return {
      stickyNav: false,
      prevScrollpos : 0,
      currentScrollPos: 0,
    }
  },
  components: {
  },
  mounted () {
    window.onscroll = () => {
      this.currentScrollPos = window.pageYOffset;
      if ( this.prevScrollpos > this.currentScrollPos && this.currentScrollPos > 400) {
        this.stickyNav = true;
      } 
      else 
      {
        this.stickyNav = false;
      }
      this.prevScrollpos = this.currentScrollPos;
    }
  },
  computed: {
    navbarClass () {
      return (this.stickyNav ? 'sticky-navbar' : 'navbar');
    }
  }
  
}
</script>

<style >

.navbar {
  background-color:var(--primary-color);
  color: #fff;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  overflow: auto;
  padding: 0 40px;
}

.navbar a {
  color: #fff;
  padding: 10px;
  margin: 0 5px;
}

.navbar a:hover {
  border-bottom: 2px solid #fff;
}

.navbar .flex {
  justify-content: space-between;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

</style>